<template>
  <div class="search">
    <div>
      <van-nav-bar left-text="返回" left-arrow @click-left="back" />
    </div>
    <van-search
      left-arrow
      v-model="value"
      placeholder="请输入搜索关键词"
      show-action
      shape="round"
      @input="getSearch(value)"
    >
      <div slot="action">搜索</div>
    </van-search>
    <div class="searchbox">
      <van-tabs v-model="active">
        <van-tab title="漫画">
          <div class="searchMH">
            <div
              class="searchlise clearfix"
              v-for="(item,index) in mhnameData.list"
              :key="index"
              @click="particularsGo({name:'particulars',params:{url:item.url}})"
            >
              <div class="pho fl">
                <img class="auto-img" :src="item.cover" alt />
              </div>
              <div class="text fl">
                <h3 class="one-text">{{item.name}}</h3>
                <p class="one-text">{{item.author}} {{item.status}}</p>
                <p class="tow-text">{{item.latest}} {{item.tag}}</p>
              </div>
            </div>
          </div>
        </van-tab>
        <van-tab title="小说">
          <div class="searchMH">
            <div
              class="searchlise clearfix"
              v-for="(item,index) in xsnameData.list"
              :key="index"
              @click="particularsGo({name:'details',params:{url:item.url}})"
            >
              <div class="pho fl">
                <img class="auto-img" :src="item.cover" alt />
              </div>
              <div class="text fl">
                <h3 class="one-text">{{item.name}}</h3>
                <p class="one-text">{{item.author}} {{item.status}}</p>
                <p class="tow-text">{{item.latest}} {{item.tag}}</p>
              </div>
            </div>
          </div>
        </van-tab>
      </van-tabs>
    </div>
  </div>
</template>

<script>
export default {
  name: "search",
  data() {
    return {
      i: 0,
      active: 2,
      value: "",
      mhnameData: [],
      xsnameData: []
    };
  },

  methods: {
    getSearch(value) {
      // console.log("value  ==> ", value);
      this.axios({
        methods: "GET",
        url: "http://api.pingcc.cn/?mhname=" + value
      }).then(result => {
        // console.log("result ==>> ", result);
        this.mhnameData = result.data;
        // console.log("this.mhnameData ==>> ", this.mhnameData);
      });

      this.axios({
        methods: "GET",
        url: "http://api.pingcc.cn/?xsname=" + value
      }).then(results => {
        // console.log("result ==>> ", results);
        this.xsnameData = results.data;
        // console.log("this.xsnameData ==>> ", this.xsnameData);
      });
    },

    particularsGo(o) {
      this.$router.push(o);
    },
    back() {
      this.$router.go(-1);
    }
  },
  created() {
    this.getSearch();
  }
};
</script>
<style >
.search .van-nav-bar {
  box-shadow: 0px 0px 5px 0px #999;
}
.search .van-nav-bar .van-icon {
  color: #e4393c;
}
.search .van-nav-bar__text {
  color: #e4393c;
  font-size: 16px;
}
.search .van-nav-bar__title {
  color: #e4393c;
  font-size: 20px;
}
.search .van-search--show-action {
  box-shadow: 0px 0px 5px 0px #999;
}
</style>
<style lang="less" scoped>
.search {
  height: 100%;
  background: whitesmoke;
}
.searchbox {
  position: fixed;
  top: 95px;
  bottom: 0;
  right: 0;
  left: 0;
  .searchMH {
    position: fixed;
    top: 138px;
    bottom: 0;
    right: 0;
    left: 0;
    overflow-y: auto;
  }
  .searchlise {
    height: 120px;
    background: white;
    margin: 5px;
    padding: 0 10px 5px;
    box-sizing: border-box;
    border-bottom: 1px solid #999;
    .pho {
      width: 25%;
      height: 100%;
      margin-right: 15px;
    }
    .text {
      width: 65%;
      text-align: center;
      h3 {
        font-size: 18px;
      }
      p {
        width: 100%;
        font-size: 16px;
        margin: 5px;
      }
    }
  }
}
</style>